<template>
    <div class="c-upload">
        <!-- 上传触发按钮 -->
        <el-button type="primary" @click="dialogVisible = true" icon="el-icon-upload">{{btn_txt}}</el-button>

        <!-- 弹出界面 -->
        <el-dialog class="c-large-dialog" title="上传" :visible.sync="dialogVisible">
            <!-- 清空按钮 -->
            <el-button
                class="u-upload-clear"
                plain
                icon="el-icon-delete"
                size="mini"
                @click="clear"
            >清空</el-button>

            <!-- 限制提示 -->
            <el-alert class="u-upload-tip" :title="tip" type="info" show-icon :closable="false"></el-alert>

            <!-- 文件区 -->
            <el-upload
                :action="API"
                list-type="picture-card"
                :auto-upload="false"
                :limit="10"
                multiple
                with-credentials
                :file-list="fileList"
                :on-change="change"
                ref="uploadbox"
            >
                <!-- :accept="accept" -->
                <i slot="default" class="el-icon-plus"></i>

                <!-- 文件项 -->
                <div
                    slot="file"
                    slot-scope="{ file }"
                    class="u-file-wrapper"
                    @click="select(file)"
                    :class="{
                        isSelected: file.selected,
                        disabled: file.status != 'success',
                    }"
                >
                    <!-- 图片类型 -->
                    <img
                        v-if="file.is_img"
                        class="el-upload-list__item-thumbnail u-imgbox"
                        :src="file.url"
                        alt
                    />
                    <!-- 其他类型 -->
                    <div v-else class="u-filebox">
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 469.333 469.333" v-bind:class="'u-fileplaceholder'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M466.208 88.458L380.875 3.125c-2-2-4.708-3.125-7.542-3.125H42.667C19.146 0 0 19.135 0 42.667v384c0 23.531 19.146 42.667 42.667 42.667h384c23.521 0 42.667-19.135 42.667-42.667V96a10.665 10.665 0 00-3.126-7.542zM106.667 21.333h234.667v128c0 11.76-9.563 21.333-21.333 21.333H128c-11.771 0-21.333-9.573-21.333-21.333v-128zM384 448H85.333V256H384v192zm64-21.333c0 11.76-9.563 21.333-21.333 21.333h-21.333V245.333a10.66 10.66 0 00-10.667-10.667h-320A10.66 10.66 0 0064 245.333V448H42.667c-11.771 0-21.333-9.573-21.333-21.333v-384c0-11.76 9.563-21.333 21.333-21.333h42.667v128C85.333 172.865 104.479 192 128 192h192c23.521 0 42.667-19.135 42.667-42.667v-128h6.25L448 100.417v326.25z"/><path d="M266.667 149.333h42.667a10.66 10.66 0 0010.667-10.667V53.333a10.66 10.66 0 00-10.667-10.667h-42.667A10.66 10.66 0 00256 53.333v85.333a10.66 10.66 0 0010.667 10.667zM277.333 64h21.333v64h-21.333V64z"/></svg>
                        <span class="u-filename">{{ file.name }}</span>
                    </div>
                    <!-- 勾选角标 -->
                    <label v-show="file.selected" class="u-file-select-label">
                        <i class="el-icon-upload-success el-icon-check"></i>
                    </label>
                </div>
            </el-upload>

            <!-- 插入按钮 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="insert">
                    {{
                    buttonTXT
                    }}
                </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import axios from "axios";
import { __cms } from "@jx3box/jx3box-common/data/jx3box.json";
const API_Root = process.env.NODE_ENV === "production" ? __cms : "/";
const API = API_Root + "api/cms/upload";

import allow_types from "@jx3box/jx3box-common/data/conf";
const imgtypes = ["jpg", "png", "gif", "bmp", "webp"];

export default {
    name: "Upload",
    props: ["text"],
    data: function () {
        return {
            API: API,
            dialogVisible: false,
            tip:
                "一次最多同时上传10个文件（不超过5M），格式限常见的图片、文档、数据表及压缩包",
            btn_txt: this.text || "上传附件",

            fileList: [],
            selectedCount: 0,
            insertList: "",

            // accept: allow_types.accept,
            // sizeLimit: allow_types.sizeLimit,
        };
    },
    watch: {
        fileList: {
            deep: true,
            handler: function (newval) {
                this.$emit("update", newval);
            },
        },
        insertList: function (newval) {
            this.$emit("htmlUpdate", newval);
        },
    },
    computed: {
        buttonTXT: function () {
            return this.selectedCount ? "插 入" : "确 定";
        },
    },
    methods: {
        change: function (file, fileList) {
            if (file.status != "success") {
                // 判断大小
                // if (file.size > this.sizeLimit) {
                //     this.$message.error("文件超出大小限制");
                //     this.removeFile(fileList, file.uid);
                //     return;
                // }

                // 构建数据
                let fdata = new FormData();
                fdata.append("file", file.raw);

                // 异步上传
                axios
                    .post(API, fdata, {
                        headers: { "Content-Type": "multipart/form-data" },
                        withCredentials: true,
                    })
                    .then((res) => {
                        // 提醒
                        this.$message({
                            message: "上传成功",
                            type: "success",
                        });

                        // 修改path
                        file.url = res.data.data && res.data.data[0];

                        // 分析文件类型
                        let ext = file.name.split(".").pop();
                        let is_img = imgtypes.includes(ext);

                        // 额外赋值
                        file.is_img = is_img;
                        file.selected = true;

                        // 修改状态加入仓库
                        file.status = "success";
                        this.fileList.push(file);
                        this.selectedCount++;
                    })
                    .catch((err) => {
                        if (err.response.data.code) {
                            this.$message.error(
                                `[${err.response.data.code}] ${err.response.data.message}`
                            );
                        } else {
                            this.$message.error("请求异常");
                        }
                    });
            }
        },
        select: function (file) {
            if (file.status == "success") {
                this.$set(file, "selected", !file.selected);
                file.selected ? this.selectedCount++ : this.selectedCount--;
            }
        },
        buildHTML: function () {
            let list = [];
            this.fileList.forEach((file) => {
                if (file.selected) {
                    file.is_img
                        ? list.push(`<img src="${file.url}" />`)
                        : list.push(
                              `<a target="_blank" href="${file.url}">${file.name}</a>`
                          );
                }
            });
            this.insertList = list.join(" \n");
            return this.insertList;
        },
        insert: function () {
            // 关闭窗口
            this.dialogVisible = false;

            //为空不执行插入
            if (!this.selectedCount) return;

            // 传递值
            this.$emit("insert", {
                list: this.fileList,
                html: this.buildHTML(),
            });

            //移除所有选择状态
            this.resetSelectStatus();
        },
        resetSelectStatus: function () {
            this.fileList.forEach((file, i) => {
                this.$set(this.fileList[i], "selected", false);
            });
            this.selectedCount = 0;
        },
        clear: function () {
            this.$refs.uploadbox.clearFiles();
            this.fileList = [];
        },
        removeFile: function (fileList, uid) {
            fileList.forEach((file, i) => {
                if (file.uid == uid) {
                    fileList.splice(i, 1);
                }
            });
        },
    },
    mounted: function () {},
    components: {},
};
</script>

<style lang="less">
@import "../assets/css/upload.less";
</style>
