            <!-- 插入按钮 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="insert">{{
                    buttonTXT
                }}</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import axios from "axios";
import { loadResource, loadStat, getIcons } from "../service/database";
import { __ossRoot, __iconPath } from "@jx3box/jx3box-common/data/jx3box.json";
import User from "@jx3box/jx3box-common/js/user";
import { school } from "@jx3box/jx3box-data/data/xf/school.json";
export default {
    name: "Resource",
    props: [],
    data: function() {
        return {
            dialogVisible: false,

            type: "skill",
            query: "",
            strict: false,

            skill: [],
            buff: [],
            item: [],
            icon: [],

            stat: {
                skill: 0,
                buff: 0,
                item: 0,
                npc: 0,
                icon: 0,
            },

            done: false,
            loading: false,

            isSuper: false,
            schools: school,

            html: "",
            isPC: true,

            per: 10,
            page: 1,
            total: 1,
            pages: 1,
        };
    },
    computed: {
        buttonTXT: function() {
            return this.selectedCount ? "插 入" : "确 定";
        },
        isBlank: function() {
            return !this.query && !this[this.type]["length"];
        },
        selectedCount: function() {
            return !!this.html;
        },
        isNumber: function() {
            return !isNaN(this.query);
        },
        hasNextPage: function() {
            return this.total > 1 && this.page < this.pages;
        },
        multipage: function() {
            return this.type !== "icon" && this.done && this.pages > 1;
        },
    },
    watch: {
        html: function(newval) {
            this.$emit("update", newval);
        },
    },
    methods: {
        getData: function(page = 1, append = false) {
            if (!this.query) return;

            this.loading = true;
            this.done = false;
            let query = this.query;
            let params = {
                strict: ~~this.strict,
                per: this.per,
                page: page,
            };

            // 图标
            if (this.type == "icon") {
                if (isNaN(query)) {
                    getIcons(query, params)
                        .then((data) => {
                            this.icon = data;
                        })
                        .finally(() => {
                            this.done = true;
                            this.loading = false;
                        });
                } else {
                    let arr = [
                        {
                            iconID: ~~this.query,
                            isSelected: false,
                        },
                    ];
                    this.icon = arr;
                    this.done = true;
                    this.loading = false;
                }

                // 非图标
            } else {
                loadResource(this.type, query, params)
                    .then((data) => {
                        if (!append) this[this.type] = [];
                        let list;
                        if (this.type == "item") {
                            list = this.transformData(data.data);
                            this.pages = data.last_page;
                            this.total = data.total;
                        } else {
                            list = this.transformData(data.list);
                            this.pages = data.pages;
                            this.total = data.total;
                        }
                        this[this.type] = this[this.type].concat(list);
                    })
                    .finally(() => {
                        this.done = true;
                        this.loading = false;
                    });
            }
        },
        search: function() {
            this.getData();
        },
        appendPage: function() {
            this.getData(++this.page, true);
        },
        changePage: function(i) {
            this.getData(i);
        },
        changeType: function() {
            this.page = 1;
            this.getData();
        },
        insert: function() {
            this.dialogVisible = false;
            this.$emit("insert", this.html);
        },
        transformData: function(data) {
            data.forEach((item) => {
                item.isSelected = false;
            });
            return data;
        },
        selectCommon: function(type, o, i) {
            this.resetItems();
            o.isSelected = true;
            this.html = `<pre data-type="${type}" data-id="${
                o.id
            }" class="e-jx3-resource">${
                this.$refs[this.type][i]["innerHTML"]
            }</pre>`;
        },
        selectItem: function(o, i) {
            this.resetItems();
            o.isSelected = true;
            this.html = `<a class="e-jx3-item e-jx3-item-q${o.Quality}" data-id="${o.id}" data-quality="${o.Quality}" target="_blank" href="${o.Link}">[${o.Name}]</a>`;
        },
        selectIcon: function(o) {
            this.resetItems();
            o.isSelected = true;
            this.html = `<img class="e-jx3-icon" src="${__iconPath}icon/${o.iconID}.png" alt="${o.iconID}"/>`;
            console.log(this.html);
        },
        resetItems: function() {
            let data = this[this.type];
            data.forEach((item) => {
                item.isSelected = false;
            });
        },
        showIcon: function(id) {
            return __iconPath + "icon/" + id + ".png";
        },
        checkUA: function() {
            this.isPC = window.innerWidth > 720;
        },
    },
    filters: {
        filterRaw: function(str) {
            return str && str.replace(/\\n/g, "\n");
        },
        iconURL: function(id) {
            return __iconPath + "icon/" + id + ".png";
        },
    },
    created: function() {
        this.checkUA();
        loadStat().then((data) => {
            this.stat = data;
        });
    },
    components: {},
};
</script>

<style lang="less">
@import "../assets/css/resource.less";
</style>
